import React,{useState} from 'react'
import propsTypes from 'prop-types'
//saludo y subtitulo es una props agregada con destructuracion de objetos
const  CounterApp= ({value = 0}) =>{


    const [counter, setCounter] = useState(value);

    const BotonSum = ()=>{
      setCounter(counter+1);
    }
    const BotonRes = ()=>{
        setCounter(counter-1);
      }
 
      const BotonReset = ()=>{
        setCounter(value);
      }
   
 
   return(
    <>
   
     <h1 id="contador" >NÃºmero Actual</h1>
        <h2 id="contador"> { counter } </h2>
        <button className="btn btn-primary" onClick={BotonSum}>+1</button>
        <button className="btn btn-danger" onClick={BotonRes}>-1</button>
        <button className="btn btn-warning" onClick={BotonReset}>Reset</button>
    </>
   );
   
   
}
// aqui agregamos typo de datos a las props
CounterApp.prototype = {
value: propsTypes.number.isRequired
}
//aqui definimos valores por defecto a las props
CounterApp.defaultProps={
   
}

export default CounterApp;